@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Ruberoid";
  font-display: swap;
  src: url("../fonts/Ruberoid-Regular.woff2") format("woff2");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Ruberoid";
  font-display: swap;
  src: url("../fonts/Ruberoid-Bold.woff2") format("woff2");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: normal;
  font-family: "Ruberoid", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.container {
  text-align: center;
  position: relative;
  max-width: 1380px;
  margin: 0 auto;
  margin-top: 40px;
  margin-bottom: 40px;
  background-color: #ffffff;
  box-shadow: 0px 0px 30px 0px rgba(0, 0, 0, 0.25);
  border-radius: 20px;
  padding: 30px;
}
@media (max-width: 1439px) {
  .container {
    max-width: 718px;
    padding: 20px;
  }
}
@media (max-width: 767px) {
  .container {
    max-width: 375px;
    padding: 10px;
  }
}

.wrapper-box {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}
.wrapper-box .header,
.wrapper-box .footer {
  flex-shrink: 0;
}
.wrapper-box main {
  flex-grow: 1;
  background-color: #e1e1e1;
  padding: 0 20px;
}

.mining {
  padding: 0;
  margin: 0;
}

.mining__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  justify-content: center;
  grid-template-columns: 427px 315px 315px 203px;
  grid-template-rows: 190px 150px 25px 11px 271px 103px;
  grid-template-areas: "bitmain pool hotel optom" "bitmain container hotel optom" "bitmain container hotel waterblock" "whatsminer container hotel waterblock" "whatsminer container electro waterblock" "whatsminer blockchain blockchain waterblock";
  column-gap: 20px;
}
@media (max-width: 1439px) {
  .mining__list {
    display: grid;
    grid-template-columns: 337px 313px;
    grid-template-rows: 365px 180px 180px 190px 190px 103px;
    grid-template-areas: "bitmain optom" "whatsminer container" "whatsminer electro" "pool waterblock" "hotel waterblock" "blockchain blockchain";
    gap: 20px;
  }
}
@media (max-width: 767px) {
  .mining__list {
    display: flex;
    flex-wrap: wrap;
  }
}

.mining__item {
  border-radius: 20px;
  background-color: #1C3F5C;
  padding: 25px 30px;
  color: #ffffff;
  position: relative;
  overflow: hidden;
  cursor: pointer;
}
.mining__item .mining__link {
  color: #ffffff;
}
.mining__item:hover .mining__image {
  transform: scale(1.05) translate(6px, -6px);
}
.mining__item:hover .mining__image-blockchain {
  transform: scale(1.05);
}
.mining__item:hover .mining__image-pool {
  transform: scale(1.05) rotate(10deg);
}
.mining__item:hover .mining__image-container {
  transform: scale(1.05) translateY(15px);
}
@media (max-width: 767px) {
  .mining__item {
    width: 325px;
    height: 385px;
  }
}

.mining__title {
  padding: 0;
  margin: 0;
  position: relative;
  font-size: 24px;
  font-weight: 700;
  line-height: normal;
  text-align: start;
}

.mining__description {
  position: absolute;
  font-size: 18px;
  font-weight: 400;
  line-height: normal;
  text-align: start;
  top: 100%;
  left: 0;
}

.mining__item-bitmain {
  grid-area: bitmain;
}

.mining__item-whatsminer {
  grid-area: whatsminer;
  background-color: #e1e1e1;
  margin-top: 20px;
}
.mining__item-whatsminer .mining__link {
  color: #00325C;
}
@media (max-width: 1439px) {
  .mining__item-whatsminer {
    margin-top: 0;
  }
}

.mining__item-pool {
  grid-area: pool;
  background-color: #0078DB;
}

.mining__item-container {
  grid-area: container;
  margin-top: 20px;
}
@media (max-width: 1439px) {
  .mining__item-container {
    margin-top: 0;
  }
}

.mining__item-blockchain {
  grid-area: blockchain;
  margin-top: 20px;
  padding-top: 13px;
}
@media (max-width: 1439px) {
  .mining__item-blockchain {
    margin: 0;
    padding: 20px;
  }
}

.mining__item-hotel {
  grid-area: hotel;
  background-color: #e1e1e1;
}
.mining__item-hotel .mining__link {
  color: #00325C;
}
@media (max-width: 1439px) {
  .mining__item-hotel .mining__image {
    width: 250px;
    height: 350px;
    left: 150px;
  }
}
@media (max-width: 767px) {
  .mining__item-hotel .mining__image {
    width: 300px;
    height: 400px;
    left: 40px;
  }
}

.mining__item-electro {
  grid-area: electro;
  background-color: #0078DB;
  margin-top: 20px;
}
@media (max-width: 1439px) {
  .mining__item-electro {
    margin-top: 0;
  }
}
@media (max-width: 767px) {
  .mining__item-electro .mining__image-electro {
    width: 250px;
    height: 300px;
  }
}

.mining__item-optom {
  grid-area: optom;
  background-color: #0078DB;
}
@media (max-width: 1439px) {
  .mining__item-optom .mining__image {
    left: 60px;
  }
}
@media (max-width: 767px) {
  .mining__item-optom .mining__image {
    width: 250px;
    height: 300px;
  }
}

.mining__item-waterblocks {
  grid-area: waterblock;
  background-color: #e1e1e1;
  margin-top: 20px;
}
.mining__item-waterblocks .mining__link {
  color: #00325C;
}
@media (max-width: 1439px) {
  .mining__item-waterblocks {
    margin-top: 0;
  }
  .mining__item-waterblocks .mining__image {
    width: 250px;
    height: 350px;
  }
}
@media (max-width: 767px) {
  .mining__item-waterblocks .mining__image {
    left: 100px;
  }
}

.mining__image {
  position: absolute;
  left: 0;
  bottom: 0;
  object-fit: cover;
  object-position: 50% 50%;
  transition: 0.3s ease;
}

.mining__image-pool {
  left: 88px;
  transform: scale(1.09);
}
@media (max-width: 1439px) {
  .mining__image-pool {
    left: 95px;
  }
}
@media (max-width: 767px) {
  .mining__image-pool {
    width: 300px;
    height: 250px;
    left: 30px;
  }
}

.mining__image-electro {
  left: 63px;
}
@media (max-width: 1439px) {
  .mining__image-electro {
    width: 150px;
    height: 150px;
    left: 163px;
  }
}
@media (max-width: 767px) {
  .mining__image-electro {
    left: 74px;
  }
}

.mining__image-blockchain {
  left: 160px;
}
@media (max-width: 1439px) {
  .mining__image-blockchain {
    width: 600px;
    height: 103px;
  }
}
@media (max-width: 767px) {
  .mining__image-blockchain {
    bottom: 40px;
    left: -2px;
  }
}

.mining__image-container {
  transform: translateY(1em);
}
@media (max-width: 1439px) {
  .mining__image-container {
    width: 150px;
    height: 200px;
    left: 164px;
  }
}
@media (max-width: 767px) {
  .mining__image-container {
    width: 250px;
    height: 300px;
    left: 0;
  }
}

.header {
  background-color: #1C3F5C;
  color: #ffffff;
  text-align: center;
  padding: 25px 30px;
}
@media (max-width: 1439px) {
  .header {
    padding: 15px 20px;
  }
}
@media (max-width: 767px) {
  .header {
    padding: 10px 15px;
  }
}

.header__title {
  margin: 0;
}

.footer {
  background-color: #1C3F5C;
  color: #ffffff;
  padding: 25px 30px;
}
@media (max-width: 1439px) {
  .footer {
    padding: 15px 20px;
  }
}
@media (max-width: 767px) {
  .footer {
    padding: 10px 15px;
    font-size: 14px;
  }
}

.footer__list {
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  gap: 10px;
  list-style: none;
}